        <el-dropdown :hide-on-click="false">
            <span class="el-dropdown-link">
                &emsp;客户测
                <i class="el-icon-caret-bottom" style="margin-left: 82px;"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
            </el-dropdown-menu>
            <span style="color: #A5AEB1; margin-left: 52%">
                当前企业为{{ this.$store.state.arr.status ? "启用" : "禁用" }}状态
                &nbsp;
                <a style="color: #6FCDFF; text-decoration: underline;" @click.prevent="xiugai">{{
                    this.$store.state.arr.status ? '禁用' : '启用' }}超级企业</a>
            </span>
        </el-dropdown>
        <div class="main">
            <div class="flexbox">
                <h2>签单</h2>
                <span>企业类型</span>
            </div>
            <div class="flexbox">
                <h2>{{ this.$store.state.arr.maxnum }}</h2>
                <span>最大坐席数</span>
            </div>
            <div class="flexbox">
                <h2>{{ this.$store.state.arr.maxconcurrency }}</h2>
                <span>最大登录并发总数</span>
            </div>
            <div class="flexbox">
                <h2>停用</h2>
                <span>API功能</span>
            </div>
            <div class="flexbox" style="border-right: none">
                <h2>启用</h2>
                <span>录音</span>
            </div>
        </div>
        <template>
            <div class="tab">
                <a-tabs default-active-key="1" @change="callback">
                    <a-tab-pane key="1" tab="企业信息配置">
                        <div style="border: 1px solid #E9F3FB">
                            <p class="spaz">
                                子企业基本信息&nbsp;<i class="el-icon-edit"></i>
                            </p>
                            <div class="footbox">
                                <div style="text-align: left;line-height: 45px;width: 700px;">
                                    <span
                                        style="text-align: right; color: #BEB6B9;display: inline-block;width: 150px;"><span
                                            style="color: #F56C6C;margin-right: 2px;">*</span>子企业名称</span>&emsp;客户测<br>
                                    <span
                                        style="text-align: right; color: #BEB6B9;display: inline-block;width: 150px;">企业类型</span>&emsp;签单<br>
                                    <span
                                        style="text-align: right; color: #BEB6B9;display: inline-block;width: 150px;"><span
                                            style="color: #F56C6C;margin-right: 2px;">*</span>截至日期</span>&emsp;{{
                                                this.$store.state.arr.adminname }}
                                </div>
                                <div style="width: 350px;"></div>
                                <div style="text-align: left;line-height: 45px;width: 700px;">
                                    <span
                                        style="text-align: right; color: #BEB6B9;display: inline-block;width: 150px;">最大坐席数</span>&emsp;{{
                                            this.$store.state.arr.maxnum }}<br>
                                    <span
                                        style="text-align: right; color: #BEB6B9;display: inline-block;width: 150px;">最大登录并发总数</span>&emsp;{{
                                            this.$store.state.arr.maxconcurrency }}<br>
                                    <span
                                        style=" text-align: right; color: #BEB6B9;display: inline-block;width: 150px;">
                                        创建时间</span>&emsp;{{
                                            this.$store.state.arr.create_time }}
                                </div>
                            </div>
                        </div>
                    </a-tab-pane>
                    <a-tab-pane key="2" tab="话机列表" force-render>
                        Content of Tab Pane 2
                    </a-tab-pane>
                    <a-tab-pane key="3" tab="号码管理">
                        Content of Tab Pane 3
                    </a-tab-pane>
                    <a-tab-pane key="4" tab="坐席管理">
                        Content of Tab Pane 3
                    </a-tab-pane>
                    <a-tab-pane key="5" tab="呼转计费配置">
                        <div class="hujiao">
                            <p style="margin-left: 12px;margin-top: 20px;">子企业呼转配置&emsp;<i class="el-icon-edit"></i></p>
                            <el-switch v-model="value1" style="margin-left: 70px;margin-top: 5px;"
                                inactive-text="呼转特殊配置">
                            </el-switch>
                        </div>
                        <div class="hujiao">
                            <p style="margin-left: 12px;margin-top: 20px;">子企业计费配置&emsp;<i class="el-icon-edit"></i></p>
                            <el-switch v-model="value1" style="margin-left: 70px;margin-top: 5px;"
                                inactive-text="计费特殊配置">
                            </el-switch>
                        </div>
                    </a-tab-pane>
                </a-tabs>
            </div>
        </template>
    </div>
</template>
<script>
import { updatestatus } from "../../api/html.js";
export default {
    data() {
        return {
            value1: true,
        };
    },
    methods: {
        callback(key) {
            console.log(key);
        },
        // 修改状态
        xiugai() {
            let a = !this.$store.state.arr.status
            let obj = {
                id: this.$store.state.arr.id,
                status: a ? 1 : 0,
            }
            updatestatus(obj)
                .then(response => {
                    console.log('状态改变成功', response);
                    this.$store.dispatch('uparr', a ? 1 : 0)
                    this.$emit("a")
                })
                .catch(error => {
                    console.error('状态改变失败', error);
                });
        },
    },
};
</script>
<style scoped>
.hujiao {
    width: 98%;
    height: 150px;
    margin: auto;
    margin-top: 12px;
    border: 1px solid #E9F3FB;
}

.spaz {
    width: 98%;
    height: 30px;
    margin-left: 25px;
    margin-top: 12px;
}

.footbox {
    width: 92%;
    height: 200px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tab {
    width: 98%;
    margin: auto;
    margin-top: 12px;
}

.main {
    width: 98%;
    height: 100px;
    margin: auto;
    margin-top: 12px;
    border: 1px solid #E9F3FB;
    display: flex;
    justify-content: center;
    align-items: center;
}

.flexbox {
    width: 200px;
    text-align: center;
    border-right: 1px solid #E9F3FB;
}

