import { apiGet, apiPost } from "./api";
// 验证码
export function getyzm() {
    return new Promise((resolve) => {
        apiGet("index.php/index/index/getcode").then((res) => {
            resolve(res);
        });
    })
}
// 登录
export function login(data) {
    return new Promise((resolve) => {
        apiGet("/index.php/index/index/login", data).then((res) => {
            resolve(res);
        });
    })
}
